// /app/events/page.js
'use client';
import React, { useEffect, useState } from 'react';
import EventCard from './components/EventCard';

const EventsPage = () => {
  const [events, setEvents] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Fetch events from your custom WP REST API
  useEffect(() => {
    const fetchEvents = async () => {
      try {
        console.log("1. Loading events...");
        const res = await fetch(`${process.env.NEXT_PUBLIC_API_WP}/custom/v1/events`);
        if (!res.ok) throw new Error("Failed to fetch events");
        const data = await res.json();
        console.log("2. Events received:", data);
        setEvents(data);
      } catch (err) {
        console.error("3. Error fetching events:", err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchEvents();
  }, []);

  if (loading) return <p>Loading events...</p>;
  if (error) return <p>Error loading events: {error}</p>;

  return (
    <div className="p-4 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
      {events.map(event => (
        <EventCard key={event.id} event={event} />
      ))}
    </div>
  );
};

export default EventsPage;

